import React from 'react';
import classNames from 'classnames';
import { HorizontalBar, Pie } from 'react-chartjs-2';
import 'chartjs-plugin-datalabels';

import {
  Card,
  CardHeader,
  CardBody,
  CardTitle,
  Row,
  Col,
  ButtonGroup,
  Button,
} from 'reactstrap';

import { domesticStatusChart } from 'variables/charts.jsx';

import DomesticStatusToday from 'components/DomesticStatus/DomesticStatusToday.jsx';
import DomesticStatusByCityMap from 'components/DomesticStatus/DomesticStatusByCityMap.jsx';
import DomesticStatusByCityTable from 'components/DomesticStatus/DomesticStatusByCityTable.jsx';
import DomesticStatusDailyChart from 'components/DomesticStatus/DomesticStatusDailyChart';
import DomesticStatusBySexDoughnut from 'components/DomesticStatus/DomesticStatusBySexDoughnut.jsx';
import DomesticStatusByAgeDoughnut from 'components/DomesticStatus/DomesticStatusByAgeDoughnut.jsx';

class Dashboard extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      domesticStatus: {},
      sexData: 'confirmator',
      ageData: 'confirmator',
    };
  }

  setSexData(data) {
    this.setState({ sexData: data });
  }
  setAgeData(data) {
    this.setState({ ageData: data });
  }

  render() {
    console.log('render()');
    return (
      <>
        <div className="content">
          {/* 국내 오늘 확진 현황 */}
          <DomesticStatusToday />
          <p className="information">
            * (+ / - ) 질병관리본부 전일 발표 대비 변화량
            <br />* 사망률 : (사망자 / 확진환자) * 100
          </p>
          {/* domesticStatusBycity Map,Table */}
          <Row>
            <Col lg="12" className="pl5 pr5">
              <Card className="card-chart">
                <CardHeader>
                  <i className="tim-icons icon-map-big text-primary mr10" />
                  <h5 className="card-category display-content">
                    시도별 발생동향
                  </h5>
                </CardHeader>
                <CardBody>
                  <DomesticStatusByCityMap />
                  <DomesticStatusByCityTable />
                </CardBody>
              </Card>
            </Col>
          </Row>
          <Row></Row>
          {/* domesticStatusDaily Chart */}
          <Row>
            <Col lg="12" className="pl5 pr5">
              <DomesticStatusDailyChart />
            </Col>
          </Row>
          {/* domesticStatusBySex Pie */}
          <Row>
            <Col lg="12" className="pl5 pr5">
              <Card className="card-chart">
                <CardHeader>
                  <Row>
                    <Col className="text-left" sm="6">
                      <i className="tim-icons icon-chart-pie-36 text-primary mr10" />
                      <h5 className="card-category display-content">
                        성별 현황
                      </h5>
                    </Col>
                    <Col sm="6">
                      <ButtonGroup
                        className="btn-group-toggle float-right"
                        data-toggle="buttons"
                      >
                        <Button
                          tag="label"
                          className={classNames('btn-simple', {
                            active: this.state.sexData === 'confirmator',
                          })}
                          color="info"
                          id="0"
                          size="sm"
                          onClick={() => this.setSexData('confirmator')}
                        >
                          <input
                            defaultChecked
                            className="d-none"
                            name="options"
                            type="radio"
                          />
                          <span className="d-none d-sm-block d-md-block d-lg-block d-xl-block">
                            확진자
                          </span>
                          <span className="d-block d-sm-none">확진자</span>
                        </Button>
                        <Button
                          color="info"
                          id="1"
                          size="sm"
                          tag="label"
                          className={classNames('btn-simple', {
                            active: this.state.sexData === 'dead',
                          })}
                          onClick={() => this.setSexData('dead')}
                        >
                          <input
                            className="d-none"
                            name="options"
                            type="radio"
                          />
                          <span className="d-none d-sm-block d-md-block d-lg-block d-xl-block">
                            사망자
                          </span>
                          <span className="d-block d-sm-none">사망자</span>
                        </Button>
                      </ButtonGroup>
                    </Col>
                  </Row>
                </CardHeader>
                <CardBody>
                  <div className="chart-area">
                    <Pie
                      redraw
                      data={DomesticStatusBySexDoughnut[this.state.sexData]}
                      options={DomesticStatusBySexDoughnut.options}
                    />
                  </div>
                </CardBody>
              </Card>
            </Col>
          </Row>
          {/* domesticStatusByAge Pie */}
          <Row>
            <Col lg="12" className="pl5 pr5">
              <Card className="card-chart">
                <CardHeader>
                  <Row>
                    <Col className="text-left" sm="6">
                      <i className="tim-icons icon-chart-pie-36 text-primary mr10" />
                      <h5 className="card-category display-content">
                        연령별 현황
                      </h5>
                    </Col>
                    <Col sm="6">
                      <ButtonGroup
                        className="btn-group-toggle float-right"
                        data-toggle="buttons"
                      >
                        <Button
                          tag="label"
                          className={classNames('btn-simple', {
                            active: this.state.ageData === 'confirmator',
                          })}
                          color="info"
                          id="0"
                          size="sm"
                          onClick={() => this.setAgeData('confirmator')}
                        >
                          <input
                            defaultChecked
                            className="d-none"
                            name="options"
                            type="radio"
                          />
                          <span className="d-none d-sm-block d-md-block d-lg-block d-xl-block">
                            확진자
                          </span>
                          <span className="d-block d-sm-none">확진자</span>
                        </Button>
                        <Button
                          color="info"
                          id="1"
                          size="sm"
                          tag="label"
                          className={classNames('btn-simple', {
                            active: this.state.ageData === 'dead',
                          })}
                          onClick={() => this.setAgeData('dead')}
                        >
                          <input
                            className="d-none"
                            name="options"
                            type="radio"
                          />
                          <span className="d-none d-sm-block d-md-block d-lg-block d-xl-block">
                            사망자
                          </span>
                          <span className="d-block d-sm-none">사망자</span>
                        </Button>
                      </ButtonGroup>
                    </Col>
                  </Row>
                </CardHeader>
                <CardBody>
                  <div className="chart-area">
                    <Pie
                      redraw
                      data={DomesticStatusByAgeDoughnut[this.state.ageData]}
                      options={DomesticStatusByAgeDoughnut.options}
                    />
                  </div>
                </CardBody>
              </Card>
            </Col>
          </Row>

          <Row>
            <Col lg="12" className="pl5 pr5">
              <Card className="card-chart">
                <CardHeader>
                  <i className="tim-icons icon-chart-bar-32 text-primary mr10" />
                  <h5 className="card-category display-content">
                    시도별 발생동향
                  </h5>
                </CardHeader>
                <CardBody>
                  <div className="chart-area">
                    <HorizontalBar
                      data={domesticStatusChart.data}
                      options={domesticStatusChart.options}
                    />
                  </div>
                </CardBody>
              </Card>
            </Col>
          </Row>

          <p className="information">출처: 질병관리본부</p>
        </div>
      </>
    );
  }
}

export default Dashboard;
